@import url("https://fonts.googleapis.com/css?family=Montserrat|Sansita");

//theme
$dark-theme: var(--theme, true) !default;
//colors
$primary-color: #FFDEDE;
$primary-color-2: #CEF0FF;
$text-color: #141736;
$secondary-color: #d93f52;
$background-color: #fbfbfb;
$sun-color: #fff385;

$primary-color-dark: #463B75;
$primary-color-2-dark: #8F55AB;
$text-color-dark: #fbfbfb;
$secondary-color-dark: #fa89aa;
$background-color-dark: #16171E;
$moon-color: #CED0E8;

//text
$font-text: "Montserrat", sans-serif;
$font-heading: "Sansita", sans-serif;

